<template>
	<b-card class="card-transaction col-lg-5 mx-auto" no-body>
		<b-card-header>
			<b-card-title>订单详情</b-card-title>
		</b-card-header>

		<b-card-body>
			<div v-for="transaction in transactionData" v-show="transaction.show" :key="transaction.mode" class="transaction-item">
				<b-media no-body>
					<b-media-body>
						<h6 class="transaction-title">
							{{ transaction.mode }}
						</h6>
						<small>{{ transaction.types }}</small>
					</b-media-body>
				</b-media>
				<div class="font-weight-bolder" :class="transaction.deduction ? 'text-danger' : 'text-success'">
					{{ transaction.payment }}
				</div>
			</div>
			<b-button type="submit" class="mt-5" block :disabled="isPosting || show || orderdata.status != 0" variant="primary" @click="pay">
				<b-spinner small v-show="isPosting || show" type="grow" />
				{{ orderdata.status == 0 ? '确认并支付 ￥' + orderdata.total_amount / 100 : statusId2Txt(orderdata.status) }}
			</b-button>
		</b-card-body>
	</b-card>
</template>

<script>
import { BCard, BCardHeader, BButton, BCardTitle, BCardBody, BMediaBody, BMedia, BMediaAside, BAvatar, BSpinner } from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	components: {
		BCard,
		BCardHeader,
		BButton,
		BCardTitle,
		BCardBody,
		BMediaBody,
		BMedia,
		BMediaAside,
		BAvatar,
		BSpinner
	},
	data() {
		return {
			transactionData: [],
			isPosting: true,
			payments: [],
			orderdata: { total_amount: 0 },
			OrderDatas: null,
			show: false,
			updateThread: null
		}
	},
	mounted() {
		this.Init()
	},
	methods: {
		pay() {
			let data = {
				trade_no: this.$route.params.id,
				method: 1
			}
			this.show = true
			this.$http({
				url: '/api/v1/user/order/checkout',
				method: 'POST',
				data: data
			})
				.then(res => {
					res = res.data.data
					if (res === true) {
						//this.$router.push({ name: 'Orders' })
					} else {
						window.location.href = res
					}
				})
				.catch(err => {
					if (err.response && err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
				})
		},
		CheckOrder() {
			this.$http({
				url: '/api/v1/user/order/check?trade_no=' + this.$route.params.id,
				method: 'GET'
			}).then(res => {
				res = res.data
				if (res.data == 1) {
					this.showToast('success', 'top-center', '支付成功', '支付成功，请等待系统确认(最多需要1分钟)')
					clearInterval(this.updateThread)
				}
			})
		},
		showToast(variant, position, title, text) {
			this.$toast(
				{
					component: ToastificationContent,
					props: {
						title: title,
						icon: 'InfoIcon',
						text: text,
						variant
					}
				},
				{
					position
				}
			)
		},
		Init() {
			this.$http({
				url: '/api/v1/user/order/details?trade_no=' + this.$route.params.id,
				method: 'GET'
			})
				.then(res => {
					res = res.data.data
					// if (res.status !== 0) {
					// 	this.$router.push({
					// 		path: '/dashboard'
					// 	})
					// }
					if (res) {
						this.orderdata = res
						this.transactionData.push({
							mode: res.plan.name + ' - ' + this.typeName2TypeTxt(res.cycle),
							types: '套餐原价',
							avatar: 'PocketIcon',
							avatarVariant: 'light-primary',
							payment: '￥' + res.plan[res.cycle] / 100,
							deduction: false,
							show: true
						})
						if (res.surplus_amount && res.surplus_amount > 0)
							this.transactionData.push({
								mode: '套餐折抵',
								types: '通过您的旧订阅折抵',
								avatar: 'PocketIcon',
								avatarVariant: 'light-primary',
								payment: '- ￥' + res.surplus_amount / 100,
								deduction: true,
								show: true
							})
						if (res.discount_amount && res.discount_amount > 0)
							this.transactionData.push({
								mode: '折扣',
								types: '优惠码优惠',
								avatar: 'PocketIcon',
								avatarVariant: 'light-primary',
								payment: '- ￥' + res.discount_amount / 100,
								deduction: true,
								show: true
							})
						if (res.balance_amount && res.balance_amount > 0)
							this.transactionData.push({
								mode: '余额支付',
								types: '通过您的余额抵扣',
								avatar: 'PocketIcon',
								avatarVariant: 'light-primary',
								payment: '- ￥' + res.balance_amount / 100,
								deduction: true,
								show: true
							})

						this.transactionData.push({
							mode: '合计支付',
							types: '您共需支付',
							avatar: 'PocketIcon',
							avatarVariant: 'light-primary',
							payment: '￥' + res.total_amount / 100,
							deduction: false,
							show: true
						})
					}
					this.isPosting = false
				})
				.catch(err => {
					this.isPosting = false
				})

			this.$http({
				url: '/api/v1/user/order/getPaymentMethod',
				method: 'GET'
			})
				.then(res => {
					res = res.data.data
					if (res) {
						this.payments = res
					}
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
				})
		},
		statusId2Txt(statusId) {
			switch (statusId) {
				case 0:
					return '订单待支付'
				case 1:
					return '订单开通中'
				case 2:
					return '订单已取消'
				case 3:
					return '订单已完成'
				case 4:
					return '订单已折抵'
			}
		},
		typeName2TypeTxt(typeId) {
			switch (typeId) {
				case 'month_price':
					return '月付'
				case 'quarter_price':
					return '季付'
				case 'half_year_price':
					return '半年付'
				case 'year_price':
					return '年付'
				case 'onetime_price':
					return '按量'
				case 'two_year_price':
					return '两年付'
				case 'three_year_price':
					return '三年付'
				case 'reset_price':
					return '流量重置包'
				default:
					return '未知'
			}
		}
	}
}
</script>
